.main-content-block__text-block{
  position: relative;
  max-width: 468px;

  &--first{
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  a:hover{
    transition: all .3s ease;
    color: #c6c6c6;
  }
}

.main-content-block__title{
  font-family: 'Cormorant Garamond';
  font-style: normal;
  font-weight: 500;
  font-size: 90px;
  line-height: 102.5%;
  text-transform: uppercase;
  color: $color-default-white;
  margin-bottom: 20px;
}

.main-content-block__text{
  font-family: 'Inter';
  font-style: normal;
  font-weight: 100;
  font-size: 14px;
  line-height: 17px;
  color: $color-default-white;
  margin-bottom: 40px;
}
